import { AdditionalButtons, StyledButton } from "./styled";

export const Buttons = ({
  tasks,
  hideDone,
  finishAllTasks,
  toggleHideDone,
}) => {
  return (
    <AdditionalButtons>
      {tasks.length > 0 && (
        <>
          <StyledButton onClick={toggleHideDone}>
            {hideDone ? "Poka≈º" : "Ukryj"} uko≈Ñczone
          </StyledButton>
          <StyledButton
            onClick={finishAllTasks}
            disabled={tasks.every(({ done }) => done)}
          >
            Uko≈Ñcz wszystkie
          </StyledButton>
        </>
      )}
    </AdditionalButtons>
  );
};
