
import { Item, List, ListText, TextRenameForm, Button } from "./styled";

export const Tasks = ({
  tasks,
  hideDone,
  newTaskContent,
  toggleTaskDone,
  renameTask,
  removeTask,
  changeTaskContent,
  cancelRenameTask,
  setNewTaskContent,
}) => {
  const onFormSubmit = (event) => {
    event.preventDefault();
  };

  return (
    <List>
      {tasks.map((task) => (
        <li key={task.id}>
          <Item hide={(task.done && hideDone) || task.rename}>
            <Button onClick={() => toggleTaskDone(task.id)}>
              {task.done ? "‚úî" : ""}
            </Button>
            <ListText done={task.done}>{task.content}</ListText>
            <Button
              rename
              onClick={() => renameTask(task.id)}
              title="edytuj zadanie"
            >
              üñäÔ∏è
            </Button>
            <Button
              remove
              onClick={() => removeTask(task.id)}
              title="usu≈Ñ zadanie"
            >
              üóë
            </Button>
          </Item>
          <TextRenameForm hide={!task.rename} onSubmit={onFormSubmit}>
            <input
              value={newTaskContent}
              type="text"
              placeholder=""
              onChange={({ target }) => setNewTaskContent(target.value)}
            />
            <Button
              type="submit"
              title="Zatwierd≈∫ zmianƒô"
              onClick={() => changeTaskContent(task.id)}
            >
              ‚úî
            </Button>
            <Button
              remove
              type="button"
              title="Anuluj zmianƒô"
              onClick={() => cancelRenameTask(task.id)}
            >
              X
            </Button>
          </TextRenameForm>
        </li>
      ))}
    </List>
  );
};
